/* Reset some default browser styles */
body, html, #root {
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
  overflow-x: hidden;
}

body {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

/* Header styling */
.header {
  background-color: #0a1f44;
  padding: 10px 20px; /* Adjust padding for better spacing */
  display: flex;
  align-items: center;
  justify-content: flex-start;
  box-sizing: border-box;
}

.header .logo {
  height: 50px;
  width: 50px;
  object-fit: cover;
  overflow: hidden;
}

/* Card styling */
.card {
  background: white;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  border-radius: 10px;
  padding: 20px;
  margin: 10px;
  box-sizing: border-box;
}

/* Chart sorting controls */
.controls {
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
}

.sort-controls, .toggle-controls {
  display: flex;
  align-items: center;
}

.sort-controls label, .toggle-controls label {
  margin-right: 10px;
}

.sort-controls select, .toggle-controls input {
  margin-right: 5px;
}

.sortable {
  cursor: pointer;
}

.sortable:hover {
  text-decoration: underline;
}

/* App styling */
.App {
  font-family: Arial, sans-serif;
  display: flex;
  flex-direction: column;
  height: 100vh;
}

.content {
  display: flex;
  flex: 1;
  flex-direction: column;
  align-items: flex-start;
  margin: 20px;
  overflow: hidden;
}

.product-card {
  width: 100%;
  margin-bottom: 20px;
  overflow-y: auto;
}

.product {
  text-align: center;
  margin-right: 20px;
  flex-grow: 1;
}

.product img {
  max-width: 100%;
  height: auto;
  margin-bottom: 20px;
}

.product .tags {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.product .tag {
  background-color: #f0f0f0;
  border-radius: 20px;
  padding: 5px 10px;
  margin: 5px;
  font-size: 14px;
}

.main-content {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  overflow-y: auto;
  width: 100%;
}

.chart, .table {
  width: 100%;
  margin-bottom: 20px;
}

.table {
  overflow-x: auto;
}

.table table {
  width: 100%;
  border-collapse: collapse;
}

.table th, .table td {
  padding: 10px;
  text-align: left;
  border-bottom: 1px solid #ddd;
}

.table th {
  background-color: #f4f4f4;
}

/* Media queries for responsiveness */
@media (min-width: 600px) {
  .content {
    flex-direction: row;
  }

  .product-card {
    width: 30%;
    margin-right: 20px;
  }

  .main-content {
    width: 70%;
  }
}

@media (max-width: 599px) {
  .content {
    flex-direction: column;
  }

  .product-card, .main-content {
    width: 100%;
  }

  .controls {
    flex-direction: column;
    align-items: flex-start;
  }

  .sort-controls, .toggle-controls {
    margin-bottom: 10px;
  }
}
